import * as React from 'react';
import {styles} from './card.module.scss';

import {Link} from "gatsby";


export const GalleryCard = ({itemData}) => {
  return (
    <div className={styles}>
      <Link className="wrap-img" to={'/gallery/'+ itemData.name}>
        <img src={'/gallery/'+itemData.name+'.jpg'}/>
      </Link>
        <h3>Author:&nbsp; <span>{itemData.author}</span></h3>
        <a rel="noreferrer" className="link" target="_blank" href={itemData.source}>
          Source: &nbsp;
          <div className="wrap-animation">
            <span className="link-animation"> {itemData.source}</span>
          </div>
        </a>
    </div>
  );
};
