import * as React from 'react';
import { GalleryCard } from './card/card';
import {styles} from './gallery.module.scss';

import {   ROUTES} from "../../config";
import AniLink from "gatsby-plugin-transition-link/AniLink";
export const Gallery = () => {

  let cardData  =[
    {name: 'webglfluid'  ,  source:'https://github.com/stepanetsaleksandr/Startup_Website' , author:'Aleksandr'},
    {name: 'cat'    , source:'https://codepen.io/Yakudoo/pen/oXJYxy' , author:'Karim Maaloul\n'},
    {name: 'blobs'     , source:'https://codepen.io/Mamboleoo/pen/BWXeqR' , author:'Louis Hoebregts'},
    {name: 'noiseblob'     , source:'https://codepen.io/Mamboleoo/pen/ExwXWyy' , author:'Louis Hoebregts'}

    ]

  return (
    <section className={styles}>
      <div className="wrap">
        <div className="button">
          <AniLink className={'link'} swipe direction="down" hex="#663399"  entryOffset={80}  duration={1} to={ROUTES.widgets}>
                <span> back</span>
          </AniLink>
        </div>
        <h2>3D Art Gallery</h2>
        <div className="cards">
          {cardData.map((item, key)=>(
            <GalleryCard key={key}  itemData={item}/>
          ))}
        </div>
      </div>
    </section>
  );
};
